.stats {
  position: relative;
  color: white;
  margin-top: 5px;
  &__descr {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  }
  &__header {
    position: relative;
    display: flex;
    justify-content: center;
    padding: 25px;
    margin-top: 50px;
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border: 1px solid rgb(201, 170, 113);
      opacity: 0.3;
    }
  }
  &__section {
    padding: 10px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    // margin-top: 20px;
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border: 1px solid rgb(201, 170, 113);
      opacity: 0.3;
    }
  }
  &__descr-name {
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      margin-right: 5px;
      height: 15px;
      width: 15px;
    }
    color: rgb(201, 170, 113);
  }
  &__descr-value {
    margin: 10px;
  }
  &__img {
    max-width: 1280px;
    height: 300px;
    overflow: hidden;
    position: relative;
    border: 1px solid rgb(201, 170, 113);
    img {
      position: relative;
      top: -100px;
      width: 100%;
    }
  }
}

.container {
  min-height: 150px;
  margin-top: 100px;
  box-shadow: 0 0 10px rgba(104, 104, 104, 0.7);
}

.spell-container {
  padding: 50px;
  display: flex;
  justify-content: space-around;
}
.spell-devider {
  display: block;
  height: 120px;
  border: 1px solid rgb(73, 73, 99);
  margin: auto 0;
  opacity: 0.5;
}
.second-spell-devider {
  width: 90%;
  border-bottom: 1px solid rgb(73, 73, 99);
  margin: 0 auto;
}

.spell {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 40px;
  border: 1px solid transparent;
  box-shadow: 0 0 0 rgba(0, 0, 0, 0);
  transition:
    border 0.3s,
    box-shadow 0.3s;

  &__active {
    border: 1px solid rgb(138, 129, 4);
    box-shadow: 0 0 10px 2px rgb(138, 129, 4);
  }
  img {
    cursor: pointer;
    max-width: 65px;
    max-width: 65px;
  }
  &__label {
    margin-top: 10px;
  }
  &__passive {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    &_label {
      margin-top: 10px;
      font-size: 16px;
    }
    img {
      cursor: pointer;
      max-width: 55px;
      max-width: 55px;
    }
  }
  &__descr-container {
    padding: 50px;
    min-height: 150px;
  }
}

.back-btn {
  position: absolute;
  width: 15px;
  height: 15px;
  top: -30px;
  left: -18px;
  transform: translateX(-50%) rotate(-45deg);
  border-left: 2px solid rgb(138, 129, 4);
  border-top: 2px solid rgb(138, 129, 4);
  transition: all 0.3s;
  cursor: pointer;
  &:hover {
    opacity: 0.7;
  }
  &::before {
    content: "";
    position: absolute;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    background-color: transparent;
  }
}
